@font-face {
 font-family: 'Roboto-Bold';
 font-style: normal;
 font-weight: 700;
 src: local('Roboto-Bold'),
 url('../fonts/Roboto-Bold.woff2') format('woff2'), /* Super Modern Browsers */
 url('../fonts/Roboto-Bold.woff') format('woff'), /* Modern Browsers */
 url('../fonts/Roboto-Bold.ttf') format('truetype'), /* Safari, Android, iOS */
 url('../fonts/Roboto-Bold.eot'), /* IE9 Compat Modes */
 url('../fonts/Roboto-Bold.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
 url('../fonts/Roboto-Bold.svg#OpenSans') format('svg'); /* Legacy iOS */
}
@font-face {
 font-family: 'Roboto-Regular';
 font-style: normal;
 font-weight: 400;
 src: local('Roboto-Regular'),
 url('../fonts/Roboto-Regular.woff2') format('woff2'), /* Super Modern Browsers */
 url('../fonts/Roboto-Regular.woff') format('woff'), /* Modern Browsers */
 url('../fonts/Roboto-Regular.ttf') format('truetype'), /* Safari, Android, iOS */
 url('../fonts/Roboto-Regular.eot'), /* IE9 Compat Modes */
 url('../fonts/Roboto-Regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
 url('../fonts/Roboto-Regular.svg#OpenSans') format('svg'); /* Legacy iOS */
}
@font-face {
 font-family: 'Roboto-Medium';
 font-style: normal;
 font-weight: 500;
 src: local('Roboto-Medium'),
 url('../fonts/Roboto-Medium.woff2') format('woff2'), /* Super Modern Browsers */
 url('../fonts/Roboto-Medium.woff') format('woff'), /* Modern Browsers */
 url('../fonts/Roboto-Medium.ttf') format('truetype'), /* Safari, Android, iOS */
 url('../fonts/Roboto-Medium.eot'), /* IE9 Compat Modes */
 url('../fonts/Roboto-Medium.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
 url('../fonts/Roboto-Medium.svg#OpenSans') format('svg'); /* Legacy iOS */
}