.hero {
    background-image: url(../img/bg-hero.png);
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    &__screen {
        max-width: 654px;
        margin-bottom: 40px;

        &-lead {
            font-size: 32px;
            line-height: 37px;
            color: #444444;
            margin-bottom: 60px;

            &--bold {
                font-weight: bold;

            }
        }

        &-text {
            font-size: 23px;
            line-height: 32px;
            color: #444;
        }
    }

    &__icon-groups {
        display: flex;
        max-width: 586.86px;
        justify-content: space-around;
    }

    &__icon-group {
        display: flex;
        flex-direction: column;
        text-align: center;
        font-size: 14px;
        line-height: 18px;
        justify-content: inherit;
        max-width: 166px;
&-img {
    width: 89px;
    margin: auto;
}
        &-txt {
            margin-top: 20px;
            text-transform: uppercase;
            color: #444444;
        }
    }
}

.hero {

    // .hero__form

    &__form {
        max-width: 431px;
        background: #FFFFFF;
        padding: 50px 63px 20px 63px;
        /* card shadow */
        // box-shadow: (0, 0, 0, 0.16), (0, 0, 0, 0.24);
        border-radius: 2px;
        position: absolute;
        left: 1000px;
        top: 123px;
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.16), 0px 2px 4px rgba(0, 0, 0, 0.24);

    }


    // .hero__form-txt

    &__form-txt {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 1.2;
        display: flex;
        color: #80A2C5;
        margin-bottom: 30px;
    }

    &__form-label {
        display: flex;
        flex-direction: column;
    }

    &__form-btn {
        width: 301px;
        height: 50px;
        background: #FF5C5C;
        box-shadow: (0, 0, 0, .16), (0, 0, 0, .24);
        border-radius: 2px;
        font-weight: bold;
        font-size: 14px;
        line-height: 16px;
        align-items: center;
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #FFFFFF;
        margin-bottom: 20px;
    }

    &__form-title {
        font-size: 23px;
        line-height: 32px;
        font-family: Roboto-Medium, sans-serif;
        color: #2E3033;
        text-align: inherit;
        margin-bottom: 28px;
    }

    &__form-txt--small {
        font-size: 10px;
        line-height: 12px;
        display: -ms-flexbox;
        display: flex;
        color: #BCBFC4;
        width: 100%;
        align-items: center;
    }
}

.cont-form-text,
.cont-form-number {
    margin-bottom: 20px;
    background: #EDEFF2;
    border: none;
    width: 300px;
    height: 30px;
    padding: 0 10px;
    /* Плейсхолдер */
    box-shadow: inset 1px 1px 4px rgba(202, 90, 90, 0.12);
    border-radius: 2px;
}

.cont-form-number,
.cont-form-text {

    &:focus {
        border: 2px solid #29B0D9;
    }
}

.cont-form-number {
    margin-bottom: 35px;
}

.small-inner {
    width: 100%;
    align-items: center;
    display: flex;
    justify-content: center;
}

.img-inner {
    margin-right: 10px;
}

::-webkit-input-placeholder {
    color: $color-placeholder
}

/* webkit */
::-moz-placeholder {
    color: $color-placeholder
}

/* Firefox 19+ */
:-moz-placeholder {
    color: $color-placeholder
}

/* Firefox 18- */
:-ms-input-placeholder {
    color: $color-placeholder
}

/* IE */