.our-production {
    padding-top: 108px;
    padding-bottom: 105px;
    background-color: #ffffff;
    border-bottom: 5px dotted rgba(0,0,0,.1);
    &__title {
        font-size: 32px;
        line-height: 32px;
        font-family: Roboto-Medium, sans-serif;
        color: #646566;
        text-align: center;
        font-weight: 500;
    }
&__slider{
 width: 100%;
}
    &__cards {
        max-width: 768px;
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 85px;
    }

    &__card {
        width: 180px;
        display: flex;
        flex-direction: column;
        text-align: center;
        margin-top: 80px;
        position: relative;
        padding: 25px 16px;

        &-txt {
            text-decoration-line: underline;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 16px;
            line-height: 23px;
            color: #939599;
            
        }
       
        & input:checked~ :before {
            content: "";
            position: absolute;
            display: inline-block;
            box-shadow: inset 1px 1px 4px rgba(0, 0, 0, 0.15);
            border-radius: 4px;
            width: 180px;
            height: 170px;
            top: 0;
            right: 0;
            bottom: -50px;
            left: 0;
            border-radius: 4px;
        }

        & input:checked~ .our-production__card-txt{
            color: #626366;
            text-decoration: none;
        }
        &-img {
            display: inline-block;
            margin-bottom: 33px;
        }
    }

    &__wrapper {
        width: 100%;
        display: flex;
        justify-content: space-around;
    }

    &__inner {
        max-width: 980px;
        width: 100%;
    }

    &__tabcontent {
        display: flex;
        text-align: start;
        justify-content: space-around;
&-img{
    display: block;
    float: left;
    margin-right: 86px;
    padding-left: 100px;
}
        &-title {
            font-weight: bold;
            font-size: 18px;
            line-height: 26px;
            display: flex;
            align-items: center;
            letter-spacing: 0.04em;
            color: #939599;
            margin-bottom: 35px;
        }

        &-txt {
            max-width: 718px;
            width: 100%;
            font-family: 'Roboto-Regular';
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 18px;
            display: flex;
            align-items: center;
            color: #626366;
            margin-bottom: 40px;
        }

        &-btn {
            background: #FFFFFF;
            border: 2px solid #FFAEAE;
            box-sizing: border-box;
            // box-shadow: (0, 0, 0, .16%), (0, 0, 0, .24%);
            box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.16), 0px 2px 4px rgba(0, 0, 0, 0.24);
            border-radius: 4px;
            font-weight: bold;
            line-height: 22px;
            letter-spacing: 0.04em;
            color: #FF5C5C;
            width: 290px;
            height: 50px;
        }
    }

    input[type=radio] {
        // display: none;
        width: 0;
        height: 0;
        position: absolute;
        z-index: -1;
        opacity: 0;
    }
    label {
        cursor: pointer;
    }
}
.slick-next slick-arrow{
    background-color:red;
}