.industry-solutions {
    padding-top: 149px;
    padding-bottom: 100px;

    &__title {
        margin-bottom: 60px;
        text-transform: uppercase;
        color: #626366;
        // letter-spacing: inherit;
    }

    &__cards {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        width: 100%;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 15px;
        color: #626366;
        font-family: Roboto, sans-serif;
    }

    &__card-list{
        list-style-type: none;
        position: relative;
    }
    &__card-list-items{
        margin-bottom: 10px;
        margin-left: 25px;
    }
    &__card-list-items::before{
        content: "";
        height: 10px;
        width: 10px;
        background: rgba(255, 174, 174, 0.8);
        position: absolute;
        border-radius: 50%;
        left: 0;
    transform: translateY(15%);
    }
    // &__card-list-items::marker{
    //     color: hotpink;
    // }
    &__card-list-items:nth-last-child(1){
        margin-bottom: 0px;
    }

    &__card-title {
        margin-bottom: 29px;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: inherit;
        text-transform: uppercase;
        color: #626366;
    }

    &__card1 {
        padding: 25px 22px;
        background-image: url('../../img/1.png');
        max-width: 273px;
        min-height: 260px;
        background-repeat: no-repeat;
        background-size: cover;
        margin-bottom: 40px;
        margin-right: 29px;
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.16), 0px 2px 4px rgba(0, 0, 0, 0.24);
        border-radius: 5px;
        // width: 100%;
    }

