.header {
  // font-weight: bold;
  // color: steelblue;
  // font-size: 60px;
  padding-top: 33px;
  padding-bottom: 63px;
  max-width: 1680px;


  &__menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 67px;
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    max-width: 780px;

    &-link {
      margin-right: 26px;
      color: #222222;

      &:nth-child(4) {
        margin-right: 0;
      }
    }
  }

  &__phone {
    display: flex;
    flex-direction: column;
  }

  &__number {
    color: #444444;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    margin-bottom: 10px;
  }

  &__back-call {
    background: #FF5C5C;
    border-radius: 2px;
    padding: 0 15px;
    line-height: 26px;
    color: #FFFFFF;
    font-family: "Roboto", sans-serif;
    font-size: 14px;
  }


}